@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500;600;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.App {
    margin-bottom: 80px;
}
button {
    border: 0;
    background: inherit;
}
.accounts {
    margin-bottom: 30px;
    max-width: 100%;
}
.accounts .heading-three {
    margin-bottom: 13px;
}
.account-container {
    max-width: 100%;
    overflow-x: auto;
}
.account-list {
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.account {
    background: linear-gradient(180deg, #4C54E1 0%, #6652E2 100%);
    box-shadow: 0px 9px 19px -4px rgba(46, 51, 136, 0.12), 0px 3px 7px rgba(36, 40, 108, 0.02);
    border-radius: 8px;
    height: 157px;
    width: 132px;
    color: #fff;
    margin-right: 16px;
    display: flex;
    flex-direction: column;
    text-decoration: none;
}
.header-four {
    font-style: normal;
    font-weight: 500;
    font-size: 11px;
    line-height: 13px;
    margin: 14px 0 0 16px;
}
.accounts .price {
    margin: 9px 0 0 14px;
}
.accounts .currency {
    font-style: normal;
    font-weight: 500;
    font-size: 18.8571px;
    line-height: 22px;
    margin-right: 3px;
    position: relative;
    top: -7px;
}
.accounts .amount {
    font-style: normal;
    font-weight: 500;
    font-size: 37.7143px;
    line-height: 44px;
}
.user-icon {
    margin: auto 0 16px 14px;
}
.transactions .heading-three {
    margin-bottom: 11px;
}
.transaction {
    background: #F9F9F9;
    border-radius: 8px;
    width: 100%;
    min-height: 54px;
    margin: 8px 0;
}
.landing {
    padding-top: 44px;
}
.page-body {
    margin: 0 24px;
}
.heading-one {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 21px;
}
.heading-three {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 16.44px;
    color: #000000;
}
.profile-icon {
    display: block;
    margin-left: auto;
    margin-right: 23px;
}
.account-detail {
    background: linear-gradient(180deg, #4C54E1 0%, #6652E2 100%);
    height: 281px;
    position: relative;
    display: flex;
    flex-direction: column;
}
.handle-page {
    padding-top: 44px;
    display: flex;
}
.back-button {
    margin-left: 19px;
}
.transactions-container {
    padding: 30px 24px 0 24px;
    background: #fff;
    margin-top: -44px;
    border-radius: 24px 24px 0 0;
    position: relative;
    z-index: 1;
}
.account-detail .header-four {
    color: #fff;
    margin-left: 24px;
    margin-top: 30px;
}
.add-button {
    border-radius: 50%;
    width: 58px;
    height: 58px;
    background: #000;
    position: relative;
    z-index: 2;
    border: 0;
    position: absolute;
    right: 24px;
    bottom: 15px;
}
.add-button::before {
    content: "";
    display: inline-block;
    width: 2.42px;
    height: 23.36px;
    border-radius: 1.61px;
    background: #fff;
}
.add-button::after {
    content: "";
    display: inline-block;
    height: 2.42px;
    width: 23.36px;
    border-radius: 1.61px;
    background: #fff;
    position: absolute;
    left: calc(50% - (23.36px / 2 ));
    top: calc(50% - (2.42px /2 ));
}
.account-detail .price {
    margin-left: 24px;
    margin-top: -13px;
    color: #fff;
    display: flex;
    align-items: flex-start;
    margin-bottom: auto;
}
.account-detail .currency {
    font-style: normal;
    font-weight: 500;
    font-size: 24.5143px;
    line-height: 29px;
    position: relative;
    top: 17px;
}
.account-detail .amount {
    font-style: normal;
    font-weight: 500;
    font-size: 65.1429px;
    line-height: 76px;
    letter-spacing: 0.065em;
}
.nav {
    background: #FFFFFF;
    box-shadow: 0px -3px 7px rgba(140, 140, 140, 0.02);
    border-radius: 24px 24px 0px 0px;
    width: 100%;
    height: 80px;
    position: fixed;
    bottom: 0;
    z-index: 5;
}

.nav ul {
    display: flex;
    align-items: flex-start;
    justify-content: space-evenly;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 19px;
    margin-bottom: 27px;
}

li.link-item {
    list-style: none;
    height: 34px;
}
.link-item a {
    display: inline-block;
    width: 43px;
    cursor: pointer;
    height: 34px;
    position: relative;
}
.link-item a::after {
    content: "";
    position: absolute;
    bottom: 0;
    display: inline-block;
    width: 100%;
    height: 3px;
    background: #C4C4C4;
    border-radius: 1.5px;
}
.link-item a.active::after {
    background: #4C54E1;
}
.link-icon {
    display: block;
    margin: 0 auto;
    width: 24px;
    height: 24px;
    border: 1px solid #C4C4C4;
    border-radius: 50%;
}
.active .link-icon {
    border: 2px solid #4C54E1;
}
* {
    font-family: "Raleway", sans-serif;
}
